"use cli"

import React, { useState, useEffect } from 'react';

function AddressInput() {
  const [address, setAddress] = useState('');

  // بارگذاری مقدار اولیه از LocalStorage
  useEffect(() => {
    const savedAddress = localStorage.getItem('address');
    if (savedAddress) {
      setAddress(savedAddress);
    }
  }, []);

  // ذخیره هر بار که مقدار تغییر کرد
  useEffect(() => {
    localStorage.setItem('address', address);
  }, [address]);

  return (
    <div className="flex flex-col items-center justify-center min-h-screen bg-black text-white p-4">
      {/* تصویر بالا */}
      <img
        src="https://via.placeholder.com/150x100.png?text=Top+Image"
        alt="Top"
        className="mb-6"
      />

      {/* Input */}
      <input
        type="text"
        value={address}
        onChange={(e) => setAddress(e.target.value)}
        placeholder="Enter your address"
        className="w-full max-w-md p-3 rounded-lg bg-gray-800 text-white border border-gray-700 focus:outline-none focus:ring-2 focus:ring-blue-500"
      />

      {/* تصویر پایین */}
      <img
        src="https://via.placeholder.com/150x100.png?text=Bottom+Image"
        alt="Bottom"
        className="mt-6"
      />
    </div>
  );
}

export default AddressInput;
